import { Tabs, Tab, Toast } from '@antmjs/vantui'

export default function Demo() {
  return (
    <Tabs
      active={1}
      onChange={(e) =>
        Toast.show({
          message: e.detail.name,
          selector: '#tabs-demo1',
        })
      }
    >
      <Tab title=" 1"> 1</Tab>
      <Tab title=" 2"> 2</Tab>
      <Tab title=" 3"> 3</Tab>
      <Tab title=" 4"> 4</Tab>
      <Toast id="tabs-demo1" />
    </Tabs>
  )
}
