import { View } from '@tarojs/components'
import { Tabs, Tab, Toast } from '@antmjs/vantui'

export default function Demo() {
  const onClick = (e) => {
    Toast.show({
      message: e.detail.title,
      selector: '#tab-demo6',
    })
  }

  return (
    <View>
      <Tabs onClick={onClick}>
        <Tab title=" 1"> 1</Tab>
        <Tab title=" 2"> 2</Tab>
      </Tabs>
      <Toast id="tab-demo6" />
    </View>
  )
}
