import { Tabs, Tab, Toast } from '@antmjs/vantui'

export default function Demo() {
  return (
    <Tabs
      onDisabled={(e) =>
        Toast.show({
          message: e.detail.title + '',
          selector: '#tabs-demo4',
        })
      }
    >
      <Tab title=" 1"> 1</Tab>
      <Tab title=" 2" disabled={true}>
         2
      </Tab>
      <Tab title=" 3"> 3</Tab>
      <Toast id="tabs-demo4" />
    </Tabs>
  )
}
