import { Tabs, Tab, Toast } from '@antmjs/vantui'

export default function Demo() {
  return (
    <Tabs
      active={'a'}
      onChange={(e) =>
        Toast.show({
          message: e.detail.name,
          selector: '#tabs-demo2',
        })
      }
    >
      <Tab title=" a" name="a">
         a
      </Tab>
      <Tab title=" b" name="b">
         b
      </Tab>
      <Tab title=" c" name="c">
         c
      </Tab>
      <Toast id="tabs-demo2" />
    </Tabs>
  )
}
